import React from "react";
import styled from "styled-components";

import { BoxContentCenter } from "../../0-base/Box";

const StyledHeaderTitle = styled(BoxContentCenter).attrs({
  as: "h1",
})`
  min-width: 169px;
  height: 64px;

  background-color: ${(props) => props.theme.color.ultramarineBlue};

  color: ${(props) => props.theme.color.white};
`;

export default function HeaderTitle(): JSX.Element {
  return <StyledHeaderTitle>R.js</StyledHeaderTitle>;
}
